const faqTabs = [...document?.querySelectorAll(".faq-list__faq")];

if (faqTabs.length > 0) {
  faqTabs.forEach((targetDetail) => {
    targetDetail.addEventListener("click", () => {
      faqTabs.forEach((detail) => {
        if (detail !== targetDetail) {
          detail.removeAttribute("open");
        }
      });
    });
  });
}
